//! PORTFOLIO

.portfolio-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow-y: auto; 
}

.portfolio-header {
  padding-left: 100px;
  padding-right: 70px;
  width: calc(100% - 150px);
  margin-top: 20px; 
}

.page-title {
  position: relative;
  text-align: center; 
  color: wheat;
  font-size: 60px;
  top: 50px;
  transform: translateY(-50%); 
  font-family: 'La Belle Aurore'; 
}

.portfolio-projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  width: 100%;
  margin-top: 10px; 
}
  
  .project-box {
    flex: 0 0 calc(33.33% - 80px);
    max-width: calc(33.33% - 80px);
    height: 200px;
    overflow: hidden;
    border-radius: 10px;
    position: relative;
    background-color: #222;
  
    .portfolio-image {
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  
    .content {
      position: absolute;
      width: 100%;
      z-index: 3;
      padding: 10px 20px;
      transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
      background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.2) 0,
        rgba(0, 0, 0, 1)
      );
      bottom: -70px;
    }
  
    .title {
      margin-bottom: 0;
      margin-top: 0;
      color: #fff;
      font-size: 18px;
      font-weight: 500;
      line-height: 24px;
    }
  
    .description {
      font-size: 14px;
      margin-bottom: 5px;
      color: #fff;
      font-weight: 700;
    }
  
    .btn {
      margin-top: 20px;
      padding: 0 23px;
      height: 32px; 
      line-height: 28px; 
      border: 2px solid #ffd700;
      border-radius: 4px;
      font-size: 14px;
      color: #fff;
      background: transparent;
      text-transform: uppercase;
      font-weight: 700;
      transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
      cursor: pointer;
      display: inline-block;
      text-align: center;
    }
  
    .btn:hover {
      transform: translateY(-3px);
      background: #bf9b30;
    }
  
    &:after {
      content: "";
      background: linear-gradient(180deg, #ffd700, #000);
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
      opacity: 0;
    }
  
    &:hover:after {
      opacity: 0.85;
    }
  
    &:hover .content {
      bottom: 0;
      background: transparent;
    }
  }
  